import Vue from "vue";
import Router from "vue-router";
import Mangas from "./views/mangas/Mangas.vue";
import Manga from "./views/mangas/Manga.vue";
import MangaReader from "./views/mangas/Reader.vue";
import Animes from "./views/animes/Animes.vue";
import Novels from "./views/novels/Novels.vue";
Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [{
      path: "/",
      name: "home",
      redirect: "/mangas"
    },
    {
      path: "/mangas",
      name: "mangas",
      component: Mangas
    },
    {
      path: "/mangas/:id",
      name: "manga",
      component: Manga
    },
    {
      path: "/mangas/:id/:chapter",
      name: "mangareader",
      component: MangaReader
    },
    {
      path: "/animes",
      name: "animes",
      component: Animes
    },
    {
      path: "/novels",
      name: "novels",
      component: Novels
    }
  ]
});