<template>
  <div app>
    <v-toolbar color="shark" dense fixed clipped-right dark app>
      <v-btn  flat icon dark @click="goBack">
        <v-icon dark>arrow_back</v-icon>
      </v-btn>
      <v-toolbar-title class="white--text">
        <router-link
           :to="{path:`/mangas/${this.$route.params.id}`,query:{source:this.$route.query.source}}" 
          class="current-chapter"
        >{{manga?manga.title:""}}</router-link>
        <span class="current-chapter">{{selectedChapter.title}}</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
    </v-toolbar>
    <v-container fill-height fluid pa-0 ma-0>
      <v-content color="mineshaft" id="kontenchapter">
        <v-layout align-center justify-center row fill-height>
          <v-flex xs12 class="text-xs-center" v-if="isLoading">
            <v-progress-linear :indeterminate="true" color="primary" ma-0></v-progress-linear>
          </v-flex>

          <v-flex v-else>
            <div
              :class="{'m-wrap':!isBook && fitPage !='width', 'auto':!isBook && fitPage !='width', 'pr':!isBook && fitPage !='width'}"
            >
              <!-- Book reader -->
              <div id="reader">
                <v-layout row wrap justify-center align-center fill-height v-if="isBook">
                  <v-flex
                    :class="{'not-visible':arahBaca == 'horizontal' && index != selectedBookPage }"
                    xs12
                    v-for="(book,index) in bookPages"
                    :key="'pageBooks'+index"
                  >
                    <v-layout
                      row
                      wrap
                      :class="{'align-center':alignPage == 'center','align-start':alignPage == 'top','align-end':alignPage == 'bottom'}"
                    >
                      <v-flex xs6 v-for="(page, i) in book" :key="'page'+i">
                        <div class="page" :class="{'margin-book':isMargin}">
                          <v-img
                            v-if="page != undefined"
                            :src="page"
                            :lazy-src="'/imgs/placeholder.png'"
                          >
                            <template v-slot:placeholder>
                              <v-layout fill-height align-center justify-center ma-0>
                                <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                              </v-layout>
                            </template>
                          </v-img>
                        </div>
                      </v-flex>
                    </v-layout>
                  </v-flex>
                </v-layout>
                <!-- Webtoon reader -->
                <v-layout row wrap justify-center fill-height align-center v-else>
                  <v-flex
                    xs12
                    :class="{'not-visible':arahBaca == 'horizontal' && index != selectedPage }"
                    v-for="(page, index) in pages"
                    :key="'page'+index"
                  >
                    <div
                      v-if="page.length >2 || page != undefined || page != ''"
                      class="page"
                      :class="{'margin-not-book':isMargin && !isBook, 'margin-book':isMargin && isBook}"
                    >
                      <v-img :src="page" :lazy-src="'/imgs/placeholder.png'">
                        <template v-slot:placeholder>
                          <v-layout fill-height align-center justify-center ma-0>
                            <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                          </v-layout>
                        </template>
                      </v-img>
                    </div>
                  </v-flex>
                </v-layout>
                <!-- </div> -->
              </div>
            </div>
            <!-- Navigasi page sidebar -->
            <div id="navigasi" v-show="drawer">
              <v-layout row>
                <v-flex xs12>
                  <v-footer dark>
                    <v-btn dark flat icon @click="prevPage">
                      <v-icon dark>chevron_left</v-icon>
                    </v-btn>
                    <v-spacer></v-spacer>
                    <div
                      class="pager"
                      v-show="arahBaca == 'horizontal'"
                    >pages {{`${isBook?selectedBookPage+1:selectedPage+1}/${isBook?bookPages.length:pages.length}`}}</div>
                    <div
                      v-show="arahBaca == 'vertical'"
                    >{{`${isBook?bookPages.legth:pages.length} Pages`}}</div>
                    <v-spacer></v-spacer>
                    <v-btn dark flat icon @click="nextPage">
                      <v-icon dark>chevron_right</v-icon>
                    </v-btn>
                  </v-footer>
                </v-flex>
              </v-layout>
            </div>
            <!-- Navigation page reader -->
            <div class="next-prev">
              <v-layout row>
                <v-flex xs6>
                  <div class="prev" @click="prevPage"></div>
                </v-flex>

                <v-flex xs6>
                  <div class="next" @click="nextPage"></div>
                </v-flex>
              </v-layout>
            </div>
            <!-- info page float reader -->
            <div class="nav-page" v-show="!drawer">
              <v-layout row align-center justify-space-between></v-layout>

              <v-flex>
                <div
                  class="pager"
                  v-show="arahBaca == 'horizontal'"
                >Page {{`${isBook?selectedBookPage+1:selectedPage+1}/${isBook?bookPages.length:pages.length}`}}</div>
                <div
                  v-show="arahBaca == 'vertical'"
                >{{`${isBook?bookPages.legth:pages.length} Pages`}}</div>
              </v-flex>
            </div>
          </v-flex>
          <v-navigation-drawer
            v-model="drawer"
            right
            fixed
            pb-60
            style="background-color:#292929"
            clipped
            app
          >
            <!-- <v-card style="background-color:#292929" dark>
            <v-card-title>-->
            <div class="c-judul">
              <h2>{{manga?manga.title:""}}</h2>
            </div>
            <!-- </v-card-title>
            <v-card-actions>-->

            <v-layout row wrap style="padding-bottom: 60px;">
              <v-flex xs12>
                <v-toolbar style="padding:0;background-color:#292929" flat class="pa-0 ma-0">
                  <!-- Previous chapter button -->
                  <v-btn flat icon dark small @click="prevChapter" :disabled="!isPrev">
                    <v-icon dark>mdi-chevron-left</v-icon>
                  </v-btn>

                  <!-- List of chapters -->
                  <v-select
                    v-if="manga"
                    v-model="selectedChapter"
                    :items="manga.chapters"
                    v-on:change="gantiChapter"
                    solo
                    dense
                    single-line
                    hide-details
                    :dark="true"
                    item-text="title"
                    item-value="slug"
                  ></v-select>
                  <v-spacer></v-spacer>

                  <v-btn flat icon dark small @click="nextChapter" :disabled="!isNext">
                    <v-icon dark>mdi-chevron-right</v-icon>
                  </v-btn>
                </v-toolbar>
                <div class="pisah-bawah"></div>
              </v-flex>

              <v-flex xs12>
                <div class="divider">
                  <v-divider></v-divider>
                </div>
                <div class="kotak">
                  <v-switch
                    @change="changeBook"
                    color="primary"
                    v-model="isBook"
                    label="Tampilkan chapter seperti buku (dua gambar per page)"
                  ></v-switch>
                  <v-switch
                    color="primary"
                    v-model="isMargin"
                    label="Enable margin per page (pemisah panel gamber dari panel lain)"
                  ></v-switch>
                  <!-- <v-switch color="primary" v-model="isLoop" label="Aktifkan kaca pembesar"></v-switch> -->
                </div>
              </v-flex>

              <v-flex xs12 text-xs-center v-show="!isBook">
                <div class="divider">
                  <v-divider></v-divider>
                </div>
                <v-subheader>Fit display</v-subheader>
                <v-btn-toggle v-model="fitPage">
                  <v-btn flat value="width">
                    <v-icon>mdi-arrow-expand-horizontal</v-icon>
                  </v-btn>
                  <!-- <v-btn v-show="arahBaca == 'horizontal'" flat value="height">
                  <v-icon>mdi-arrow-expand-vertical</v-icon>
                  </v-btn>-->
                  <v-btn flat value="container">
                    <v-icon>mdi-border-none-variant</v-icon>
                  </v-btn>
                </v-btn-toggle>
                <div class="pisah-bawah"></div>
              </v-flex>
              <v-flex xs12 text-xs-center>
                <div class="divider">
                  <v-divider></v-divider>
                </div>
                <v-subheader>Arah Baca</v-subheader>
                <v-btn-toggle v-model="arahBaca" mandatory @click="changeArahBaca">
                  <v-btn flat value="vertical">Vertikal</v-btn>
                  <v-btn flat value="horizontal">Horizontal</v-btn>
                </v-btn-toggle>
              </v-flex>
              <v-flex v-show="isBook" xs12 text-xs-center>
                <div class="pisah-bawah"></div>
                <div class="divider">
                  <v-divider></v-divider>
                </div>
                <v-subheader>Align Page</v-subheader>
                <v-btn-toggle v-model="alignPage" mandatory>
                  <v-btn flat value="top">
                    <v-icon>vertical_align_top</v-icon>
                  </v-btn>
                  <v-btn flat value="center">
                    <v-icon>vertical_align_center</v-icon>
                  </v-btn>
                  <v-btn flat value="bottom">
                    <v-icon>vertical_align_bottom</v-icon>
                  </v-btn>
                </v-btn-toggle>
              </v-flex>

              <!-- Action buttons -->
            </v-layout>

            <!-- </v-card-actions>
            </v-card>-->
          </v-navigation-drawer>

          <v-snackbar color="red" v-model="snackbar" center :timeout="3000">
            {{ infoPage }}
            <v-btn color="white" flat @click="snackbar = false">Close</v-btn>
          </v-snackbar>
        </v-layout>
      </v-content>
    </v-container>
  </div>
</template>



<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      drawer: true,
      selectedChapter: null,
      selectedPage: 0,
      fitDisplay: "container",
      chapters: null,
      currentPage: 0,
      isLoading: true,
      isNext: true,
      isPrev: true,
      isBook: false,
      isMargin: true,
      arahBaca: "horizontal",
      alignPage: "top",
      snackbar: false,
      isLoop: false,
      infoPage: "",
      fitPage: "container",
      bookPages: [],
      selectedBookPage: 0
    };
  },
  created() {
    this.isLoading = true;
  },
  mounted() {
    if (this.manga == null) {
      this.getManga();
    } else {
      this.getChapter();
    }
  },
  methods: {
    getChapter() {
      let query = {
        id: this.$route.params.chapter,
        source: this.$route.query.source
      };
      this.$store.dispatch("mangas/GET_CHAPTER", query).then(() => {
        this.gantiIndex();
        this.selectedChapter = this.manga.chapters[this.currentPage];
        this.selectedBookPage = 0;
        this.selectedPage = 0;
        this.checkNextPrev();
        this.splitPage();
        this.isLoading = false;
      });
    },
    getManga() {
      let query = {
        id: this.$route.params.id,
        source: this.$route.query.source
      };
      this.$store.dispatch("mangas/GET_MANGA", query).then(() => {
        this.getChapter();
      });
    },
    changeBook() {
      // console.log(isBook)
      if (this.isBook) {
        this.fitPage = "width";
      }
    },
    changeArahBaca(arah) {
      if ((arah = "horizontal")) {
        this.fitPage = "container";
      }
    },
    gantiChapter(slug) {
      this.$router.replace({
        path: `/mangas/${this.$route.params.id}/${slug}`,
        query: { source: this.$route.query.source }
      });
      this.isLoading = true;
      this.getChapter();

      this.selectedChapter = this.manga.chapters[this.currentPage];
    },
    gantiIndex() {
      this.currentPage = this.manga.chapters.findIndex(chapter => {
        return chapter.slug == this.$route.params.chapter;
      });
    },
    nextChapter() {
      this.currentPage--;
      this.selectedChapter = this.manga.chapters[this.currentPage];
      this.$router.replace({
        path: `/mangas/${this.$route.params.id}/${this.selectedChapter.slug}`,
        query: { source: this.$route.query.source }
      });
      this.checkNextPrev();
      this.isLoading = true;
      this.getChapter();
    },
    prevChapter() {
      this.currentPage++;
      this.selectedChapter = this.manga.chapters[this.currentPage];
      this.$router.replace({
        path: `/mangas/${this.$route.params.id}/${this.selectedChapter.slug}`,
        query: { source: this.$route.query.source }
      });
      this.checkNextPrev();
      this.isLoading = true;

      this.getChapter();
    },
    checkNextPrev() {
      if (this.currentPage == 0) {
        this.isNext = false;
      } else {
        this.isNext = true;
      }
      if (this.currentPage == this.manga.chapters.length - 1) {
        this.isPrev = false;
      } else {
        this.isPrev = true;
      }
    },
    nextPage() {
      if (this.arahBaca == "vertical") {
        window.scrollBy(0, window.innerHeight);
      } else {
        if (this.isBook) {
          if (this.selectedBookPage == this.bookPages.length - 1) {
            // this.infoPage = "Ini halaman panel terakhir tidak ada halaman lagi";
            // this.snackbar = true;
            if (this.isNext) {
              this.nextChapter();
            } else {
              this.infoPage = "Ini chapter terakhir, tidak ada chapter lagi!";
              this.snackbar = true;
            }
          } else {
            this.selectedBookPage++;
            this.$scrollTo("#kontenchapter", 250);
          }
        } else {
          if (this.selectedPage == this.pages.length - 1) {
            // this.infoPage = "Ini halaman panel terakhir tidak ada halaman lagi";
            // this.snackbar = true;
            if (this.isNext) {
              this.nextChapter();
            } else {
              this.infoPage = "Ini chapter terakhir, tidak ada chapter lagi!";
              this.snackbar = true;
            }
          } else {
            this.selectedPage++;
            this.$scrollTo("#kontenchapter", 250);
          }
        }
      }
    },
    prevPage() {
      if (this.arahBaca == "vertical") {
        window.scrollBy(0, -window.innerHeight);
      } else {
        if (this.isBook) {
          if (this.selectedBookPage == 0) {
            if (this.isPrev) {
              this.prevChapter();
            } else {
              this.infoPage = "Ini chapter paling awal!";
              this.snackbar = true;
            }
            this.selectedBookPage--;
            this.$scrollTo("#kontenchapter", 250);
          }
        } else {
          if (this.selectedPage == 0) {
            if (this.isPrev) {
              this.prevChapter();
            } else {
              this.infoPage = "Ini chapter paling awal!";
              this.snackbar = true;
            }
          } else {
            this.selectedPage--;
            this.$scrollTo("#kontenchapter", 250);
          }
        }
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    splitPage() {
      let book = [];
      for (var i = 0; i < this.pages.length; i += 2) {
        book.push([this.pages[i], this.pages[i + 1]]);
      }
      this.bookPages = book;
    }
  },
  computed: mapState({
    pages: state => state.mangas.chapter,
    manga: state => state.mangas.manga
  })
};
</script>

