import Axios from 'axios'
// initial state
const state = {
  source:"komikcast",
  mangas:[],
  manga: {},
  chapter: []
}

// getters
const getters = {
  MANGA: state => state.manga,
  MANGAS: state => state.manga,
  CHAPTER: state => state.chapter
}

// actions
const actions = {
  GET_MANGAS: async (context, query) => {
    try {
      let response = await Axios.get('http://45.76.148.33:8080/api/kirin/v1/mangas/browse',{
        params: query
      })
      // this.skills = response
      context.commit('SET_MANGAS', response.data)
    } catch (err) {
      // console.log(err)
    }
  },
  INFINITE_MANGAS: async (context, query) => {
    try {
      let response = await Axios.get('http://45.76.148.33:8080/api/kirin/v1/mangas/browse',{
        params: query
      })
      // this.skills = response
      context.commit('APPEND_MANGAS', response.data)
      // return response.data
    } catch (err) {
      // console.log(err)
    }
  }, 
  INFINITE_SEARCH: async (context, query) => {
    try {
      let response = await Axios.get('http://45.76.148.33:8080/api/kirin/v1/mangas/search',{
        params: query
      })
      // this.skills = response
      context.commit('APPEND_MANGAS', response.data)
    } catch (err) {
      // console.log(err)
    }
  }, 
  SEARCH: async (context, query) => {
    try {
      let response = await Axios.get('http://45.76.148.33:8080/api/kirin/v1/mangas/search',{
        params: query
      })
      // this.skills = response
      context.commit('SET_MANGAS', response.data)
    } catch (err) {
      // console.log(err)
    }
  }, 
  GET_MANGA: async (context, query) => {
    try {
      let response = await Axios.get('http://45.76.148.33:8080/api/kirin/v1/mangas/manga',{
        params:query
      })
      // this.skills = response
      context.commit('SET_MANGA', response.data)
    } catch (err) {
      // console.log(err)
    }

  },
  GET_CHAPTER: async (context, query) => {
    try {
      let response = await Axios.get('http://45.76.148.33:8080/api/kirin/v1/mangas/chapter',{
        params:query
      })
      context.commit('SET_CHAPTER', response.data)
    } catch (err) {
      // console.log(err)
    }

  }
}

// mutations
const mutations = {
  SET_MANGAS: (state, payload) => {
    state.mangas = payload
  },
  SET_MANGA: (state, payload) => {
    state.manga = payload
  },
  SET_CHAPTER: (state, payload) => {
    state.chapter = payload
  },
  APPEND_MANGAS: (state, payload) => {
    for(let i in payload){
      state.mangas.push(payload[i])
    }
  }

}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}