    <v-content>
      <div class="g-wrap">
        <v-layout row wrap justify-center align-center>
         
          <v-flex xs12 class="text-xs-center" v-if="isLoading">
            <v-progress-linear :indeterminate="true" color="primary" ma-0></v-progress-linear>
          </v-flex>
          <v-flex md12 v-else>
            <div id="konten">
              <v-layout row wrap>
                <v-flex v-for="(manga, index) in mangas" :key="index" xs4 sm2 md2 lg2>
                  <div class="card-container" v-ripple @click="getManga(manga)">
                    <v-card spect-ratio="0.5" dark>
                      <v-img :src="manga.cover" aspect-ratio="0.7" :lazy-src="'/imgs/placeholder.png'">
                         <template v-slot:placeholder>
                              <v-layout fill-height align-center justify-center ma-0>
                                <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                              </v-layout>
                            </template>
                      </v-img>

                      <div class="meta-info">
                        <h3 class="card-judul">{{manga.title}}</h3>
                        <div class="meta">{{manga.date}}</div>
                      </div>
                    </v-card>
                  </div>
                </v-flex>
              </v-layout>
            </div>
          </v-flex>
          <!-- <div class="infinite">
            <v-layout row justify-center align-center>
             
            </v-layout>
          </div>-->
          <v-flex xs12 class="text-xs-center" v-show="isLoadingNext">
            <v-progress-linear :indeterminate="true" color="primary" ma-0></v-progress-linear>
          </v-flex>

          <v-flex xs12 v-show="!isLoading">
            <div class="inf text-xs-center">
              <v-btn fab small color="primary" @click="next">
                <v-icon>keyboard_arrow_down</v-icon>
              </v-btn>
            </div>
          </v-flex>
           
        </v-layout>
