          reset
          <!-- <v-icon right>replay</v-icon> -->
        </v-btn>
        <!-- </v-flex>
        <v-flex>-->
        <v-btn color="primary" @click="search">
          Cari
          <!-- <v-icon right>search</v-icon> -->
        </v-btn>
        <!-- </v-flex> -->
      </v-layout>
    </div>
    <v-navigation-drawer
      v-model="drawerManga"
      right
      fixed
      pb-60
      style="background-color:#292929"
      clipped
      app
    >
      <v-container style="padding-bottom:60px">
        <v-layout column>
          <!-- <div class="c-judul">
            <h3>Pencarian</h3>
          </div>-->
          <v-subheader style="padding:0">Basic</v-subheader>
          <v-select v-model="selectedSource" :items="source" label="Sumber" box></v-select>
          <v-text-field
            label="Judul"
            placeholder="One punch man"
            box
            v-model="searchTitle"
            persistent-hint
            clearable
            hint="Jika judul diisi maka genre, status dan order diabaikan"
          ></v-text-field>
          <v-divider></v-divider>
          <v-subheader style="padding:0">Advance</v-subheader>

          <v-select
            v-model="selectedStatus"
            :items="status"
            box
            label="Status"
            dark
            item-text="title"
            item-value="value"
          ></v-select>
          <v-select
            v-model="selectedOrder"
            :items="order"
            box
            label="Order"
            dark
            item-text="title"
            item-value="value"
          ></v-select>
          <v-select
            v-model="selectedGenres"
            :items="genres"
            chips
            label="Genre"
            multiple
            dark
            box
            item-text="label"
            item-value="val"
          ></v-select>
        </v-layout>
      </v-container>
    </v-navigation-drawer>
  </div>
