<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      status: [
        {
          title: "All",
          value: ""
        },
        {
          title: "Ongoing",
          value: "ongoing"
        },
        {
          title: "Complete",
          value: "completed"
        }
      ],
      order: [
        {
          title: "A-Z",
          value: "title"
        },
        {
          title: "ZA",
          value: "titlereverse"
        },
        {
          title: "Update",
          value: "update"
        },
        {
          title: "Added",
          value: "latest"
        },
        {
          title: "Popular",
          value: "popular"
        }
      ],
      genres: [
        { label: "4-Koma", val: "4-koma" },
        { label: "Action", val: "action" },
        { label: "Adventure", val: "adventure" },
        { label: "Cars", val: "cars" },
        { label: "Comedy", val: "comedy" },
        { label: "Completed", val: "completed" },
        { label: "Cooking", val: "cooking" },
        { label: "Dementia", val: "dementia" },
        { label: "Demons", val: "demons" },
        { label: "Doujinshi", val: "doujinshi" },
        { label: "Drama", val: "drama" },
        { label: "Ecchi", val: "ecchi" },
        { label: "Fantasy", val: "fantasy" },
        { label: "Game", val: "game" },
        { label: "Gender Bender", val: "gender-bender" },
        { label: "Harem", val: "harem" },
        { label: "Historical", val: "historical" },
        { label: "Horror", val: "horror" },
        { label: "Isekai", val: "isekai" },
        { label: "Josei", val: "josei" },
        { label: "Kids", val: "kids" },
        { label: "Magic", val: "magic" },
        { label: "Manga", val: "manga" },
        { label: "Manhua", val: "manhua" },
        { label: "Manhwa", val: "manhwa" },
        { label: "Martial Arts", val: "martial-arts" },
        { label: "Mature", val: "mature" },
        { label: "Mecha", val: "mecha" },
        { label: "Military", val: "military" },
        { label: "Music", val: "music" },
        { label: "Mystery", val: "mystery" },
        { label: "Old Comic", val: "old-comic" },
        { label: "One Shot", val: "one-shot" },
        { label: "Oneshot", val: "oneshot" },
        { label: "Parodi", val: "parodi" },
        { label: "Parody", val: "parody" },
        { label: "Police", val: "police" },
        { label: "Psychological", val: "psychological" },
        { label: "Romance", val: "romance" },
        { label: "Samurai", val: "samurai" },
        { label: "School", val: "school" },
        { label: "School Life", val: "school-life" },
        { label: "Sci-Fi", val: "sci-fi" },
        { label: "Seinen", val: "seinen" },
        { label: "Shoujo", val: "shoujo" },
        { label: "Shoujo Ai", val: "shoujo-ai" },
        { label: "Shounen", val: "shounen" },
        { label: "Shounen ai", val: "shounen-ai" },
        { label: "Slice of Life", val: "slice-of-life" },
        { label: "Sports", val: "sports" },
        { label: "Super Power", val: "super-power" },
        { label: "Supernatural", val: "supernatural" },
        { label: "Thriller", val: "thriller" },
        { label: "Tragedy", val: "tragedy" },
        { label: "Vampire", val: "vampire" },
        { label: "Webtoons", val: "webtoons" },
        { label: "Yaoi", val: "yaoi" },
        { label: "Yuri", val: "yuri" }
      ],
      source: ["kiryuu", "komikcast", "pecintakomik"],
      isLoading: true,
      isLoadingNext: false,
      searchTitle: null,
      drawerManga: true,
      selectedSource: "pecintakomik",
      selectedOrder: "update",
      selectedStatus: "",
      selectedGenres: [],
      currentPage: 1
    };
  },
  created() {
    if (this.$route.query.source) {
      this.selectedSource = this.$route.query.source;
    }
  },
  mounted() {
    this.browseMangas();
  },

  computed: mapState({
    mangas: state => state.mangas.mangas
  }),

  methods: {
    search() {
      this.currentPage = 1;
      if (typeof this.searchTitle == "string") {
        if (this.searchTitle.length > 1) {
          this.searchMangas();
        } else {
          this.browseMangas();
        }
      } else {
        this.browseMangas();
      }
    },
    getManga(manga) {
      this.$router.push({
        path: `/mangas/${manga.slug}`,
        query: {
          source: this.selectedSource
        }
      });
    },
    searchMangas() {
      this.isLoading = true;
      let query = {
        source: this.selectedSource,
        s: this.searchTitle,
        page: this.currentPage
      };
      this.$store.dispatch("mangas/SEARCH", query).then(() => {
        this.isLoading = false;
      });
    },
    browseMangas() {
      this.isLoading = true;
      let query = {
        page: this.currentPage,
        source: this.selectedSource,
        status: this.selectedStatus,
        order: this.selectedOrder,
        genres: this.selectedGenres.join(",")
      };
      this.$store.dispatch("mangas/GET_MANGAS", query).then(() => {
        this.isLoading = false;
      });
    },

    next() {
       
      if (typeof this.searchTitle == "string") {
        if (this.searchTitle.length > 1) {
          this.infSearch();
        } else {
          this.infMangas();
        }
      } else {
        this.infMangas();
      }
    },

    infMangas() {
      this.isLoadingNext = true;
      this.currentPage++;
      let query = {
        page: this.currentPage,
        source: this.selectedSource,
        status: this.selectedStatus,
        order: this.selectedOrder,
        genres: this.selectedGenres.join(",")
      };
      this.$store.dispatch("mangas/INFINITE_MANGAS", query).then(() => {
        this.isLoadingNext = false;
      });
    },

    infSearch() {
      this.isLoadingNext = true;
      this.currentPage++;
      let query = {
        source: this.selectedSource,
        s: this.searchTitle,
        page: this.currentPage
      };
      this.$store.dispatch("mangas/SEARCH", query).then(() => {
        this.isLoadingNext = false;
      });
    },

    reset() {
      this.searchTitle = "";
      this.selectedSource = "komikcast";
      this.selectedOrder = "latest";
      (this.selectedStatus = ""), (this.selectedGenres = []);
      this.browseMangas();
    }
  }
};
</script>

