<template>
  <div app>
    <v-toolbar color="shark" dense fixed clipped-right dark app>
      <v-btn flat icon dark @click="goBack">
        <v-icon dark>arrow_back</v-icon>
      </v-btn>
      <v-toolbar-title class="white--text">
        <div class="current-chapter">{{manga.title.replace("Bahasa Indonesia","")}}</div>
      </v-toolbar-title>
      <!-- <v-spacer></v-spacer>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>-->
    </v-toolbar>
    <v-content>
      <v-layout column justify-start>
        <v-flex xs12 class="text-xs-center" v-if="isLoading">
            <v-progress-linear :indeterminate="true" color="primary" ma-0></v-progress-linear>
          </v-flex>
        <v-flex>
          <div id="m-header" :style="{ backgroundImage: 'url(' + manga.cover + ')' }">
            <div class="g-wrap">
              <v-layout align-center justify-start row fill-height>
                <!-- <v-flex > -->
                <div class="m-poster">
                  <v-img :lazy-src="'/imgs/placeholder.png'" :src="manga.cover" aspect-ratio="0.7">
                    <template v-slot:placeholder>
                      <v-layout fill-height align-center justify-center ma-0>
                        <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                      </v-layout>
                    </template>
                  </v-img>
                </div>
                <!-- </v-flex> -->
                <v-flex>
                  <div class="m-info">
                    <div class="m-status">
                      <span>{{manga.status.replace("Status:","")}}</span>
                    </div>
                    <div class="m-judul">{{manga.title.replace("Bahasa Indonesia","")}}</div>
                    <div class="m-author">{{'by '+manga.author}}</div>
                    <div class="m-rating">
                      <v-rating
                        v-model="rating"
                        background-color="orange lighten-3"
                        color="orange"
                        small
                        readonly
                      ></v-rating>
                      <span class="rate">{{rating}}</span>
                    </div>
                  </div>
                </v-flex>
              </v-layout>
            </div>
          </div>
        </v-flex>
        <v-flex>
          <div dark class="g-wrap">
            <v-tabs dark slider-color="white">
              <v-tab key="1" v-ripple>Informasi</v-tab>
              <v-tab key="2" v-ripple>Daftar Isi</v-tab>
              <v-tab-item key="1">
                <div class="info-manga">
                  <div class="m-title">Synopsis</div>
                  <div
                    class="synopsis"
                  >{{manga.synopsis?manga.synopsis:"Belum ada synopsis yang ditulis, silahkan googling untuk mengetahui synopsis"}}</div>
                  <div class="m-title">Genres</div>
                  <div class="m-genres">
                    <span
                      class="m-genre"
                      v-for="(genre, index) in manga.genres"
                      :key="'genre'+index"
                    >{{genre}}</span>
                  </div>
                </div>
              </v-tab-item>
              <v-tab-item key="2">
                <v-layout row wrap class="chapter-list">
                  <v-flex
                    v-ripple
                    @click="getChapter(chapter)"
                    class="chap"
                    md4
                    xs6
                    v-for="(chapter,index) in manga.chapters"
                    :key="'chapter'+index"
                  >
                    <li>
                      <a>
                        <span class="number">{{manga.chapters.length - index}}</span>
                        <div class="m-chapter">
                          <strong class="chapter-title">{{chapter.title}}</strong>
                          <small></small>
                        </div>
                      </a>
                    </li>
                  </v-flex>
                </v-layout>
              </v-tab-item>
            </v-tabs>
          </div>
        </v-flex>
      </v-layout>
    </v-content>
  </div>
</template>


<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      isLoading: true,
      rating: 0.0
    };
  },
  mounted() {
    this.getManga();
  },

  computed: mapState({
    manga: state => state.mangas.manga
  }),

  methods: {
    getChapter(chapter) {
      this.$router.push({
        path: `/mangas/${this.$route.params.id}/${chapter.slug}`,
        query: { source: this.$route.query.source }
      });
    },
    getManga() {
      this.isLoading = true;
      let query = {
        id: this.$route.params.id,
        source: this.$route.query.source
      };
      this.$store.dispatch("mangas/GET_MANGA", query).then(() => {
        this.isLoading =false
        try {
          let rating = (parseFloat(this.manga.rating) / 2).toFixed(1);
          this.rating = parseFloat(rating);
        } catch (error) {
          console.log(error);
          this.rating = 0.0;
        }
      });
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

